.Content {
    padding: 0%;
    position: relative;
    height: 100%;
    font-family: 'Pixsel', arial;
}

.form {
    padding: 10px;
}

.paginator {
    position: absolute;
    top: 2vh;
    width: 700px;
    height: 25px;
}

.button {
    margin-top: 10px;
    margin-bottom: 10px;
    position: relative;
    font-family: 'Pixsel', arial;
    outline: none;
    text-decoration: none;
    left: 5%;
    top: 3%;
    height: 4vh;
    width: 20%;
    min-width: 110px;
    padding: 0px;
    background: #C3C3C3;
    box-shadow: inset -2px -2px 0px #262626, inset 2px 2px 0px #F0F0F0, inset -4px -4px 0px #7E7E7E;
    border: none;
}

.button:active {
    padding: 0.6%;
    box-shadow: inset -2px -2px 0px #F0F0F0, inset 2px 2px 0px #262626, inset -4px -4px 0px #7E7E7E;
}

.Footer {
    position: absolute;
    bottom: 30px;
    left: 25px;
  }

.item {
    position: relative;
    display:inline-block;
    height: 100%;
    margin-left: 1.5vh;
    padding: 1.2vh;
    background: #C3C3C3;
    box-shadow: inset -2px -2px 0px #262626, inset 2px 2px 0px #F0F0F0, inset -4px -4px 0px #7E7E7E;
    font-size: 25px;
    font-family: 'Pixsel', arial;
}

.itemCoosing {
    position: relative;
    display:inline-block;
    height: 100%;
    margin-left: 1.5vh;
    padding: 1.2vh;
    background: #C3C3C3;
    box-shadow: inset 2px 2px 0px #262626, inset -2px -2px 0px #F0F0F0, inset 4px 4px 0px #7E7E7E;
    font-size: 25px;
    font-family: 'Pixsel', arial;
}


.WinsBloks {
    padding-left: 2%;
    width: 100%;
    height: 70%;
    position: absolute;
    top: 80px;
    display: grid;
    grid-template-areas: 
    "Block1 Block2";
}

@media (max-width: 1300px) {
    .WinsBloks {
        padding-left: 2%;
        width: 100%;
        height: 80%;
        position: absolute;
        top: 80px;
        display: grid;
        grid-template-areas: 
        "Block1"
        "Block2";
    }
}
.WinsBloks1 {
    padding-left: 2%;
    height: 80%;
    position: absolute;
    top: 80px;
    display: grid;
    grid-template-areas: 
    "Block1"
    "Block2";
}
