.Lable {
    padding-top: 35px;
    font-size: 20px;
    height: 35px;
    width: 700px;
}

.WinnersBlock {
    width: 650px;
}

.Footer {
  position: absolute;
  bottom: 50px;
  left: 15px;
}

.button {
  margin-top: 10px;
  margin-bottom: 10px;
  position: relative;
  font-family: 'Pixsel', arial;
  outline: none;
  text-decoration: none;
  left: 2%;
  top: 3%;
  height: 4vh;
  width: 20%;
  min-width: 110px;
  padding: 0px;
  background: #C3C3C3;
  box-shadow: inset -2px -2px 0px #262626, inset 2px 2px 0px #F0F0F0, inset -4px -4px 0px #7E7E7E;
  border: none;
}

.info {
  width: 300px;
}

.inf {
  color: #000EA3;
}

.button:active {
  padding: 0.6%;
  box-shadow: inset -2px -2px 0px #F0F0F0, inset 2px 2px 0px #262626, inset -4px -4px 0px #7E7E7E;
}


.table-wrap {
  text-align: center;
  display: inline-block;
  background-color: #fff;
  padding: 2rem 2rem;
  color: #000;
}

table {
    border: 1px solid #ccc;
    width: 100%;
    margin:0;
    padding:0;
    border-collapse: collapse;
    border-spacing: 0;
  }

table tr {
    background: #FFFFFF;
    border: 1px solid #ddd;
    padding: 5px;
  }

table th, table td {
    padding: 10px;
    text-align: center;
    border-right: 1px solid #ddd;
  }

table th {
    background: #C3C3C3;;
    box-shadow: inset -2px -2px 0px #262626, inset 2px 2px 0px #F0F0F0, inset -4px -4px 0px #7E7E7E;
    font-size: 14px;
    letter-spacing: 1px;
  }
